/* eslint-disable max-len */
/* eslint-disable no-undef */
/* eslint-disable no-param-reassign */
/* eslint-disable no-bitwise */
/* eslint-disable no-var */
/* eslint-disable vars-on-top */
/* eslint-disable no-redeclare */
/* eslint-disable no-case-declarations */
/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable no-unused-expressions */
import _ from 'lodash';

declare global {
	interface Window {
		PUBLIC_URL: any;
		dataLayer: any;
		requestAnimFrame: any;
		onload: any;
		particles: any;
		canvasZoom: any;
	}
}
interface Options {
	chars: string[];
	particlesBackToOrigin: boolean;
	mode: string;
	screenMode: string;
}

export const createCanvas = (self: any, options: Options) => {
	let canvas: any;
	let context: any;
	let loopLogic: any;
	const particlesNumber = (options.mode === 'pattern') ? 1089 : 625;
	const particles: any = [];
	const text: any = [];
	let nextText = [];
	const shape: any = {};
	let mouse = {
		x: -99999,
		y: -99999,
	};
	let currentTransition = options?.mode || 'random';
	let left: any; let right: any;
	let layout = 0;
	// const type = ['random', 'uniform', 'drop', 'ribbon'];
	const type = ['random', 'uniform', 'pattern'];
	const FPS = 660;
	window.particles = particles;
	/*
	* List colors.
	*/

	const colors = {
		random: ['#FFF', '#FFF'],
		uniform: ['#FFF', '#FFF'],
		drop: ['#1d75cf', '#3a5945'],
		ribbon: ['#702744', '#f98d00'],
	};

	/*
	 * Init.
	 */

	function init() {
		window.removeEventListener('initCanvas', init, false);
		const slideshowContainer = document.querySelector('.ip-slideshow');
		const existingCanvas = document.querySelector('canvas');

		if (existingCanvas) {
			slideshowContainer?.removeChild(existingCanvas);
			canvas = null;
		}

		canvas = document.createElement('canvas');

		onResize();

		slideshowContainer?.appendChild(canvas);

		// Browser supports canvas?
		if (capable()) {
			context = canvas.getContext('2d');

			// Events
			if ('ontouchmove' in window) {
				canvas.addEventListener('touchup', onTouchUp, false);
				canvas.addEventListener('touchmove', onTouchMove, false);
			} else {
				canvas.addEventListener('mousemove', onMouseMove, false);
				canvas.addEventListener('mouseout', onMouseOut, false);
			}

			// Arrows
			handleClick('bind', 'left');
			handleClick('bind', 'right');

			window.onresize = onResize;

			createParticles();

			// Arrows elements
			left = document.querySelector('.ip-nav-left');
			right = document.querySelector('.ip-nav-right');

			// Show right arrow
			right?.classList.add('ip-nav-show');
		} else {
			console.error('Sorry, switch to a better browser to see this experiment.');
		}
	}

	/*
	 * Checks if browser supports canvas element.
	 */

	function capable() {
		return canvas.getContext && canvas.getContext('2d');
	}

	/*
	 * On resize window event.
	 */

	function onResize() {
		if (options?.screenMode === 'fullscreen') {
			canvas.width = window.innerWidth * 4;
			canvas.height = window.innerHeight * 4;
		} else if (options?.screenMode === 'halfscreen') {
			const root = document.documentElement;
			const sandElem = document.querySelector('.sandtable-board');
			if (sandElem) {
				canvas.width = sandElem?.clientWidth * 4;
				canvas.height = sandElem?.clientHeight * 4;
				root.style.setProperty('--sand-w', `${sandElem?.clientWidth}px`);
				root.style.setProperty('--sand-h', `${sandElem?.clientHeight}px`);
			}
		} else {
			canvas.width = 1920;
			canvas.height = 1440;
		}
		updateTransition();
	}

	function scrollX() {
		return window.pageXOffset || window.document.documentElement.scrollLeft;
	}

	function scrollY() {
		return window.pageYOffset || window.document.documentElement.scrollTop;
	}

	/*
	 * Mouse move event.
	 */

	function onMouseMove(event: any) {
		event.preventDefault();
		const refScaler: any = document.querySelector('.react-transform-element');
		const scale = refScaler ? (1 / (refScaler.getBoundingClientRect().width / refScaler.offsetWidth)) * 4 : 1;
		const mousePosX = event.pageX;
		const mousePosY = event.pageY;
		const canvasLeft = scrollX() + canvas.getBoundingClientRect().left;
		const canvasTop = scrollY() + canvas.getBoundingClientRect().top;
		mouse.x = (mousePosX - canvasLeft) * scale;
		mouse.y = (mousePosY - canvasTop) * scale;
	}

	const resetMouse = () => {
		// Reset mouse position
		mouse = {
			x: -1000,
			y: -1000,
		};
	};

	const onMouseOut = () => {
		resetMouse();
	};

	/*
	 * Touch up event.
	 */

	function onTouchUp(event: any) {
		event.preventDefault();
		resetMouse();
	}

	/*
	 * Touch move event.
	 */

	function onTouchMove(event: any) {
		event.preventDefault();
		mouse.x = event.touches[0].pageX - (scrollX() + canvas.getBoundingClientRect().left);
		mouse.y = event.touches[0].pageY - (scrollY() + canvas.getBoundingClientRect().top);
	}

	/*
	 * Left click event.
	 */

	function onLeftClick(event: any) {
		event.preventDefault();

		currentTransition = type[Math.max(0, --layout)];

		// Reset towards points, and fill with new
		nextText = [];
		// updateText();

		if (layout === 0) {
			left.classList.remove('ip-nav-show');
			handleClick('unbind', 'left');
			return;
		}

		if (layout === type.length - 1) {
			right.classList.add('ip-nav-show');
			handleClick('bind', 'right');
		}
	}

	/*
	 * Right click event.
	 */

	function onRightClick(event: any) {
		event.preventDefault();

		currentTransition = type[Math.min(type.length, ++layout)];

		// Reset towards points, and fill with new
		nextText = [];
		// updateText();

		if (layout === 1) {
			left?.classList.add('ip-nav-show');
			handleClick('bind', 'left');
		}
		if (layout === type.length - 1) {
			if (right?.classList.contains('ip-nav-show')) {
				right?.classList.remove('ip-nav-show');
			}
			handleClick('unbind', 'right');
		}
	}

	/*
	 * Handle click events for arrows.
	 */

	function handleClick(action: any, tarType: any) {
		const direction = tarType === 'left' ? onLeftClick : onRightClick;

		switch (action) {
			case 'bind':
				const targetBind: any = document.querySelector(`.ip-nav-${tarType}`);
				if (targetBind) {
					targetBind.addEventListener('touchstart', direction, false);
					targetBind.addEventListener('click', direction, false);
					targetBind.style.cursor = 'pointer';
				}
				break;

			case 'unbind':
				const targetUnbind: any = document.querySelector(`.ip-nav-${tarType}`);
				if (targetUnbind) {
					targetUnbind.removeEventListener('touchstart', direction, false);
					targetUnbind.removeEventListener('click', direction, false);
					targetUnbind.style.cursor = 'default';
				}
				break;

			default:
				break;
		}
	}

	/*
	 * Create particles.
	 */

	function createParticles() {
		for (let quantity = 0, len = particlesNumber; quantity < len; quantity++) {
			var x; var y; var steps;

			// var steps = Math.PI * 2 * quantity / len;

			// x = canvas.width * 0.5 + 10 * Math.cos(steps);
			// y = 180 + 10 * Math.sin(steps);
			const stepCount = quantity;
			const uniformFactor = Math.floor(Math.sqrt(particlesNumber)); // = row and col number

			const adjIndex = stepCount + 1;
			const colCount = Math.ceil(adjIndex / uniformFactor);
			const rowCount = (adjIndex % uniformFactor === 0) ? uniformFactor : adjIndex % uniformFactor;

			// console.log('uniformFactor', uniformFactor);
			// console.log('rowCount', rowCount);
			// console.log('colCount', colCount);

			var steps: any = stepCount / len;
			const textIndex = rowCount + (uniformFactor * (uniformFactor - colCount));
			const patternIndex = colCount + (uniformFactor * (rowCount - 1));

			var initX = randomBetween(0, canvas.width);
			var initY = randomBetween(0, canvas.height);
			x = Math.floor(canvas.width / 2);
			y = initY - randomBetween(0, canvas.height * 3);

			const radius = randomBetween(0, 1);
			const hasBorn = !(radius > 0 || radius < 12);

			const color = colors.random[~~(Math.random() * colors.random.length)];

			particles.push({
				friction: 0,

				// char: randomChar(),
				char: randomChar(options.mode === 'pattern' ? patternIndex : textIndex),
				initX,
				initY,
				x,
				y,

				hasBorn,

				ease: 0.04 + Math.random() * 0.06,
				bornSpeed: 0.07 + Math.random() * 0.07,

				alpha: 0,
				maxAlpha: 0.4 + Math.random() * 0.5,

				radius,
				maxRadius: 3,

				color,
				interactive: false,

				angle: 0,

				steps,
				stepCount,
				textIndex,

			});
		}

		// Go!
		// updateText();
		if (loopLogic) {
			window.cancelAnimationFrame(loopLogic);
		}
		loop();
	}

	/*
	 * Transitions handler.
	 */

	function updateTransition() {
		/* --- Shapes ---- */
		[].forEach.call(particles, (particle: any, index) => {
			switch (currentTransition) {
				case 'random':

					shape.x = particle.initX;
					shape.y = particle.initY;
					break;

				case 'uniform':
				case 'pattern':
					const uniformFactor = Math.floor(Math.sqrt(particlesNumber));
					const rowCount = Math.floor(particle.stepCount / uniformFactor);
					const colCount = particle.stepCount % uniformFactor;

					const rowUnit = canvas.width / uniformFactor;
					const colUnit = canvas.height / uniformFactor;

					shape.x = rowCount * rowUnit;
					shape.y = 40 + colCount * colUnit;

					// var limit; var
					// 	steps;

					// limit = (particles.length * 0.5) - 1;
					// steps = Math.PI * 2 * (index / limit);

					// // First oval
					// if (index < [].slice.call(particles, 0, limit).length) {
					// 	shape.x = canvas.width * 0.5 + 80 * Math.cos(steps);
					// 	shape.y = 180 + 140 * Math.sin(steps);
					// } else {
					// 	// Second oval
					// 	limit = (particles.length * 0.5);

					// 	shape.x = canvas.width * 0.5 + 140 * Math.cos(steps);
					// 	shape.y = 180 + 80 * Math.sin(steps);
					// }

					break;

				case 'ribbon':

					shape.x = canvas.width * 0.5 + 90 * (Math.sin(index)) * Math.cos(index);
					shape.y = 320 + 140 * (-Math.sin(index) - 1);

					break;

				default:
					break;
			}

			// speed
			const speed = 0.08 * (1 - particle.friction);
			if (!particle.interactive) {
				// particle.x += ((shape.x + Math.cos(particle.angle) * 5) - particle.x) * speed;
				// particle.y += ((shape.y + Math.sin(particle.angle) * 5) - particle.y) * speed;
				const targetDistX = shape.x - particle.x;
				const targetDistY = shape.y - particle.y;
				if (!particle.initialized || options.particlesBackToOrigin) {
					particle.x += targetDistX * speed;
					particle.y += targetDistY * speed;
					particle.angle += 0.0001;
					particle.initialized = (targetDistX < 0.05 && targetDistY < 0.05);
				}
			} else {
				// particle.x += ((mouse.x + Math.sin(particle.angle) * 120) - particle.x) * 0.08;
				// particle.y += ((mouse.y + Math.cos(particle.angle) * 120) - particle.y) * 0.08;
				particle.x -= (mouse.x - particle.x) * speed;
				particle.y -= (mouse.y - particle.y) * speed;
				particle.angle += 0.08;
			}

			// particle.angle += 0.08;
		});

		// Remove extra particles
		if (nextText.length < text.length) {
			const extra = [].slice.call(text, nextText.length, text.length);

			// Remove extra text particles
			for (let index = 0; index < extra.length; index++) text.splice(index, 1);
		}
	}

	/*
	 * Loop logic.
	 */

	function loop() {
		clear();
		update();
		render();

		loopLogic = window.requestAnimFrame(loop);
	}

	/*
	 * Clear the whole screen.
	 */

	function clear() {
		context.clearRect(0, 0, canvas.width, canvas.height);
	}

	/*
	 * Update the particles.
	 */

	function update() {
		updateTransition();

		[].forEach.call(particles, (particle: any, index) => {
			particle.alpha += (particle.maxAlpha - particle.alpha) * 0.05;

			if (particle.hasBorn) {
				particle.radius += (0 - particle.radius) * particle.bornSpeed;

				if (Math.round(particle.radius) === 0) {
					// Color transition
					switch (currentTransition) {
						case 'random':

							particle.color = colors.random[~~(Math.random() * colors.random.length)];

							break;

						case 'uniform':

							particle.color = colors.uniform[~~(Math.random() * colors.uniform.length)];

							// const rowUnit = canvas.width / uniformFactor;
							// const colUnit = canvas.width / uniformFactor;

							break;

						case 'pattern':

							particle.color = colors.uniform[~~(Math.random() * colors.uniform.length)];
							if (particle.char === '｜') {
								particle.alpha = 0.4;
								particle.maxAlpha = 0.4;
							} else {
								particle.alpha = 0.75;
								particle.maxAlpha = 0.75;
							}

							break;

							// case 'pattern':

							// 	// particle.color = colors.drop[~~(Math.random() * colors.drop.length)];
							// 	const uniformFactor = Math.floor(Math.sqrt(particlesNumber));
							// 	const rowCount = Math.floor(particle.stepCount / uniformFactor);
							// 	const colCount = particle.stepCount % uniformFactor;

							// 	const circleDiameter = Math.floor(uniformFactor / 2);
							// 	const circleStart = Math.floor(uniformFactor * 0.35);
							// 	const circleEnd = Math.floor(uniformFactor * 0.85);

							// 	if (colCount > circleStart) {
							// 		const colIndex = Math.abs(rowCount - circleDiameter);
							// 		if ((colIndex * colIndex) / 4 > (circleEnd - colCount) * 4) {
							// 			particle.alpha = 0.1;
							// 			particle.maxAlpha = 0.1;
							// 		}
							// 	}

							// 	break;

						case 'ribbon':

							particle.color = colors.ribbon[~~(Math.random() * colors.ribbon.length)];

							break;

						default:
							break;
					}

					particle.hasBorn = false;
				}
			}

			if (!particle.hasBorn) {
				particle.radius += (particle.maxRadius - particle.radius) * particle.bornSpeed;

				if (Math.round(particle.radius) === particle.maxRadius) particle.hasBorn = true;
			}

			const pureMouseParticleDistance = distanceTo(mouse, particle) - particle.radius;
			const maxInteractiveEdge = 240;
			const minInteractiveEdge = 120;

			if (pureMouseParticleDistance <= maxInteractiveEdge) {
				// Have interaction
				particle.interactive = true;
				particle.friction = (pureMouseParticleDistance - minInteractiveEdge) / (maxInteractiveEdge - minInteractiveEdge);
				if (particle.friction > 0.8) {
					particle.friction = 1;
				}
				// particle.friction = 0;
			} else {
				// No interaction
				particle.interactive = false;
				particle.friction = 0;
			}
		});
	}

	/*
	 * Render the particles.
	 */

	function render() {
		[].forEach.call(particles, (particle: any, index) => {
			context.save();
			context.globalAlpha = particle.alpha;
			context.fillStyle = particle.color;
			context.font = `${50}px Lato, Arial, sans-serif`;
			context.fillText(particle.char, particle.x, particle.y);
			context.beginPath();
			// context.arc(particle.x, particle.y, particle.radius, 0, Math.PI * 2);
			context.fill();
			context.restore();
		});
	}

	/*
	 * Distance between two points.
	 */

	function distanceTo(pointA: any, pointB: any) {
		const dx = Math.abs(pointA.x - pointB.x);
		const dy = Math.abs(pointA.y - pointB.y);

		return Math.sqrt(dx * dx + dy * dy);
	}

	/*
	 * Useful function for random integer between [min, max].
	 */

	const randomBetween = (min: any, max: any) => ~~(Math.random() * (max - min + 1) + min);

	const randomChar = (index: number) => {
		const basicChars = ['未', '來'];
		if (options.mode === 'pattern') {
			if (index) {
				const patternEncoded = '';
				const chars = ['｜', ...Array.prototype.map.call(patternEncoded, (eachLetter) => eachLetter)];
				return (chars[index] === '1') ? basicChars[Math.floor(Math.random() * basicChars.length)] : '｜';
			}
			const randomChars = [...basicChars, '｜'];
			return randomChars[Math.floor(Math.random() * randomChars.length)];
		}
		const chars = ['｜', ...Array.prototype.map.call(options.chars || '｜', (eachLetter) => eachLetter)];
		return chars[index || Math.floor(Math.random() * chars.length)] || '｜';
	};

	/*
	 * Request new frame by Paul Irish.
	 * 60 FPS.
	 */

	window.requestAnimFrame = (function () {
		return window.requestAnimationFrame
			|| window.webkitRequestAnimationFrame
			|| window.mozRequestAnimationFrame
			|| window.oRequestAnimationFrame
			|| window.msRequestAnimationFrame

			|| function (callback) {
				window.setTimeout(callback, 1000 / FPS);
			};
	}());

	window.addEventListener('initCanvas', init, false);
};

export const initCanvas = () => {
	const someEv: any = document.createEvent('HTMLEvents');
	someEv.initEvent('initCanvas', true, true);
	someEv.eventName = 'initCanvas';
	document.dispatchEvent(someEv);
};

export default createCanvas;
