import React, { useEffect, useState } from 'react';
import Particles from 'react-tsparticles';
import { TransformWrapper, TransformComponent } from 'react-zoom-pan-pinch';
import SandTest2 from '../SandTest2/SandTest2';

import './SandTest1.scss';

interface Props {
	title?: string;
	children?: JSX.Element[] | JSX.Element | string;
}


export const SandTest1 = ({ title, children }: Props) => {
	const [inputMsg, setInputMsg] = useState<string>('');
	const [msgs, setMsgs] = useState<string[]>(['這不是', '真正的', '文案']);
	const [mask, setMask] = useState<string>('');
	const [particlesBackToOrigin, setParticlesBackToOrigin] = useState<boolean>(true);
	const [mode, setMode] = useState<string>('random');
	const [editTextMode, setEditTextMode] = useState<boolean>(false); // Edit Msg
	const [editMsg, setEditMsg] = useState<string>('');
	const [fullscreen, setFulScreen] = useState<boolean>(false);

	useEffect(() => {
		setEditMsg(msgs.join('___'));
	}, [msgs]);

	// Language
	console.log('SandTest1 rendered');

	const addMsg = () => {
		setMsgs([
			...msgs,
			inputMsg,
		]);
		setInputMsg('');
	};

	const saveEditedMsg = () => {
		const editedMsg: string[] = editMsg.split('___');
		setMsgs(editedMsg);
		setEditTextMode(false);
	};

	const cancelEditedMsg = () => {
		// setMsgs([
		// 	...msgs,
		// 	inputMsg,
		// ]);
		// setInputMsg('');
		setEditMsg(msgs.join('___'));
		setEditTextMode(false);
	};


	const params: any = {
		fpsLimit: 120,
		background: {
			color: {
				value: '#323031',
			},
			position: '50% 50%',
			repeat: 'no-repeat',
			size: 'cover',
		},
		interactivity: {
			events: {
				onClick: {
					enable: true,
					mode: 'push',
				},
				onHover: {
					enable: true,
					mode: 'repulse',
					parallax: {
						force: 60,
					},
				},
			},
			modes: {
				repulse: {
					distance: 120,
					duration: 3,
				},
				grab: {
					distance: 50,
				},
			},
		},
		particles: {
			color: {
				value: '#ffffff',
			},
			collisions: {
				enable: true,
			},
			// move: {
			// 	attract: {
			// 		// rotate: {
			// 		// 	x: 600,
			// 		// 	y: 1200,
			// 		// },
			// 	},
			// 	enable: true,
			// 	outModes: {
			// 		default: 'bounce',
			// 		bottom: 'bounce',
			// 		left: 'bounce',
			// 		right: 'bounce',
			// 		top: 'bounce',
			// 	},
			// 	speed: 10,
			// },
			number: {
				density: {
					enable: true,
				},
				arrangement: 'equidistant',
				value: 600,
			},
			opacity: {
				animation: {
					speed: 1,
					minimumValue: 0.1,
				},
			},
			shape: {
				options: {
					char: {
						fill: true,
						font: 'Roboto',
						style: '',
						value: (msgs.length > 0) ? msgs : ['字'],
						weight: '400',
					},
				},
				type: 'char',
			},
			size: {
				value: 10,
				animation: {
					speed: 40,
					minimumValue: 0.1,
				},
			},
		},
		polygon: {
			// draw: {
			// 	enable: !!mask,
			// 	stroke: {
			// 		color: {
			// 			value: 'rgba(255,255,255,0.2)',
			// 		},
			// 		width: 0.5,
			// 		opacity: 0.2,
			// 	},
			// },
			enable: !!mask,
			inline: {
				arrangement: '',
			},
			move: {
				radius: 10,
				type: 'path',
			},
			scale: 1.5,
			type: 'inline',
			url: mask,
		},
	};

	return (
		<div
			className={`sand-test ${fullscreen ? 'is-fullscreen' : ''}`}
		>
			<div
				className="sand-test__body"
			>
				<div
					className="sand-test__board"
				>
					<h2>SAND TABLE TEST 1</h2>
					<div className="sand-board">
						<TransformWrapper
							defaultScale={1}
							// defaultPositionX={-960}
							// defaultPositionY={450}
							options={{
								minScale: 1,
								maxScale: 4,
							}}
						>
							{({
								zoomIn, zoomOut, resetTransform, ...rest
							}: any) => (
								<>
									<TransformComponent>
										<div
											className="sand-board__zoomer"
										>
											<div
												className="sand-board__body"
											>
												{/* <Particles
													width="1920px"
													height="1440px"
													params={params}
												/> */}
												<SandTest2
													chars={msgs}
													particlesBackToOrigin={particlesBackToOrigin}
													mode={mode}
													fullscreen={fullscreen}
												/>
											</div>
										</div>
									</TransformComponent>
									<div className="tools">
										<button type="button" title="Fullscreen" className="fullscreen-btn" onClick={() => setFulScreen(!fullscreen)}>[ ]</button>
										<button type="button" title="Zoom In" onClick={zoomIn}>+</button>
										<button type="button" title="Zoom Out" onClick={zoomOut}>-</button>
										<button type="button" title="Reset Zoom" onClick={resetTransform}>x</button>
									</div>
								</>
							)}
						</TransformWrapper>
					</div>
					<nav>
						<span className="ip-nav-left" />
						<span className="ip-nav-right" />
					</nav>
				</div>
				<div
					className="sand-test__control"
				>
					<div
						className="sand-msg"
					>
						<h3>Messages</h3>
						{editTextMode ? (
							<div
								className="sand-msg-editpanel"
							>
								<textarea
									className="sand-msg-textarea"
									onChange={(e) => setEditMsg(e.target.value)}
								>
									{editMsg}
								</textarea>
								<div>
									<button
										className="custom-button"
										type="button"
										onClick={() => saveEditedMsg()}
									>
										Save
									</button>
									<button
										className="custom-button"
										type="button"
										onClick={() => cancelEditedMsg()}
									>
										Cancel
									</button>
								</div>
							</div>
						) : (
							<>
								<p>
									{`${msgs.join('___')}...`}
									<button
										className="custom-button"
										type="button"
										onClick={() => setEditTextMode(true)}
									>
										Edit
									</button>
								</p>
								<div
									className="sand-msg-input"
								>
									<input
										type="text"
										value={inputMsg}
										placeholder="Leave your msg"
										onChange={(e) => setInputMsg(e.target.value)}
									/>
									<button
										type="button"
										onClick={() => addMsg()}
									>
										Submit
									</button>
								</div>
							</>
						)}
					</div>
					<div className="sand-options">
						<h3>Options</h3>
						<div className="sand-option-group">
							<h4 className="sand-option-label">Particles move back?</h4>
							<select
								onChange={(e) => setParticlesBackToOrigin((e.target.value === 'Y'))}
							>
								<option value="Y">Yes</option>
								<option value="N">No</option>
							</select>
						</div>
						<div className="sand-option-group">
							<h4 className="sand-option-label">Particles Arrangement</h4>
							<select
								onChange={(e) => setMode(e.target.value)}
							>
								<option value="random">Random</option>
								{/* <option value="uniform">Uniform</option> */}
								<option value="pattern">Pattern</option>
							</select>
						</div>
					</div>
					{/* <div className="sand-mask">
						<h3>Mask</h3>
						<select
							onChange={(e) => setMask(e.target.value)}
						>
							{
								sandMasks.map((item: any, index: number) => {
									const key = `mask-${index}`;
									return (
										<option
											value={item.url}
											key={key}
										>
											{item.label}
										</option>
									);
								})
							}
						</select>
					</div> */}
				</div>
			</div>
		</div>
	);
};

export default SandTest1;
