import React, { useEffect, useState } from 'react';
import './PatternCreater.scss';

interface Props {
	title?: string;
	children?: JSX.Element[] | JSX.Element | string;
}

const canvasSize = 33;

export const PatternCreater = ({ title, children }: Props) => {
	// Language
	// const { t } = useTranslation();
	// const lang = t('key');
	const [onPoints, setOnPoints] = useState<Array<number>>(new Array(canvasSize * canvasSize).fill(0));

	useEffect(() => {
		const savedPoints = localStorage.getItem('onPoints');
		if (!savedPoints) return;
		const optionsArr: any = Array.prototype.map.call(savedPoints || '', (eachLetter) => Number(eachLetter));
		setOnPoints(optionsArr);
	}, []);


	const togglePoint = (index: number) => {
		console.log('togglePoint', index);
		const newPoints = [...onPoints];
		newPoints[index] = onPoints[index] ? 0 : 1;
		setOnPoints(newPoints);
		localStorage.setItem('onPoints', newPoints.join(''));
	};

	return (
		<div className="pattern-creater">
			<div className="pattern-creater__board">
				{
					onPoints.map((item: any, index: number) => (
						<button
							className={`${onPoints[index] ? 'is-active' : ''}`}
							key={`point-${index + 1}`}
							type="button"
							onClick={() => togglePoint(index)}
						>
							<span className="a11y-hidden">point</span>
						</button>
					))
				}
			</div>

			<div className="pattern-creater__result">{onPoints.join('')}</div>
		</div>
	);
};

export default PatternCreater;
