import React from 'react';
import {
	BrowserRouter as Router,
	Switch,
	Route,
} from 'react-router-dom';
import DemoLayout from '../layout/DemoLayout/DemoLayout';
import MainLayout from '../layout/MainLayout/MainLayout';
import PatternCreater from '../components/PatternCreater/PatternCreater';

export default () => (
	<Router>
		<Switch>
			<Route exact path="/pattern" component={PatternCreater} />
			<Route exact path="/:lang?/demo" component={DemoLayout} />
			<Route exact path="/:lang?" component={MainLayout} />
		</Switch>
	</Router>
);
